import { PersonSVGIcon } from '@react-md/material-icons';
import logo from '../images/watsbulkAsset 7.svg';

import { navigate } from 'gatsby';
import React , {useEffect, useState} from 'react';
import account from '../images/icons8-account-32.png';
import './sidebar_links_animation.css'
import Button from 'react-bootstrap/Button';
import { verify } from '../api_handlers/account_api';

const sidebar = {
	position: 'fixed',
    background: 'rgba(0,103,86,255)',
    boxShadow: '0 8px 32px 0 rgba(0,103,86,255)',
    backdropFilter: 'blur( 16.5px )',
    width: '11rem',
    height: 'calc(100vw)',
    padding: '0px 20px',
    display: 'flex',
    flexFlow: 'column',
    alignItems: 'stretch',
}

const logoContainer = {
	display:'flex',
	alignItems:"flex-end",
	marginTop:'2rem',
	color:'#fff'
}

const logo_ = {
	width:'2.5rem',
	margin:'0 10px'
}
const linkWrapper = {
	display:'flex',
	flexFlow:'column',
	alignItems:'stretch',
	padding:15
}

const imageWraper = {
	display:'flex',
	justifyContent:'center',
	alignItems:'center',
	width:'8rem',
	height:'8rem'
}

const image = {
	padding:'1rem',
	border:'1px solid  rgb(42 38 42 / 81%)',
	borderRadius:'50%',
	width:'7rem'
}
const accountWrapper = {
	display:'flex',
	flexFlow:'column',
	justifyContent:'space-between',
	margin:'1rem 0'
}
const shopName = {

}

const City = {
	color:'#eee',
	textAlign:'center',
	fontWeight:550,
	fontSize:'.85rem'
}

const link={
    textRendering: "optimizeLegibility",
    margin: '10px 0',
	cursor:'pointer'
};

const SelectedLink = {
	border:" 2px solid #fff",
    textRendering: "optimizeLegibility",
    borderRadius: 10,
    margin: '10px 0',
    background:'#eee',
    color:'#222'
}
const text = {
	color:'#eee',
	textAlign:'center',
	fontWeight:670
}
const link_p={
	...text,
	margin:"10px 0",
}
const link_black = {
	...link_p,
	color:'#222'
}


export function Sidebar({selected}){
	const [selection , setSelection] = useState(selected);
	const [sellerData , setSellerData] = useState({shop_name:'',city:'',profile_picture:''})
	
	let [action , setAction]= useState({text:'',action:''})
	useEffect(()=>{
	  setAction({text:'Login',action:()=>{
		localStorage.removeItem('CSRF_token');
		navigate('/login')}
	  })
	  let store_data = localStorage.getItem('store_')
	  if(store_data && typeof store_data === 'object' ){
	  setSellerData({...JSON.parse(store_data)});
	}
	},[])
	useEffect(()=>{
		let token = localStorage.getItem('CSRF_token')
  if(token === 'undefined' || token === 'null' || !token){
      navigate('/login')
  }
  if(token){
	  if(token){
		verify()
		.then(isMatch=>{
			if(!isMatch){
				if(location.href.includes('signup')){
				  setAction({
					text:'Login',action:()=>{
					  localStorage.removeItem('CSRF_token');
			navigate('/login')}
					})
					return
				  }
				  if(location.href.includes('login')){
					setAction({
					  text:'Sign up',action:()=>{
						localStorage.removeItem('CSRF_token');
			  navigate('/signup')}
					  })
					  return
					}
			}
		  if(!isMatch.success){
			navigate('/login');
		  }
		  if(isMatch.success){
			let store_ = JSON.parse(localStorage['store_']);
			store_.email = isMatch.email;
			store_.number = isMatch.number;
			localStorage.setItem('store_',JSON.stringify(store_));
			setAction(i=>{
			  return {
				text : 'Logout',
				action:()=>{
				localStorage.removeItem('CSRF_token');
				navigate('/login')}
			  } });
			if(location.href.includes('login') || location.href.includes('signup')){
			//   navigate('/products');
			console.log('navigating to products')
			}
		  }
	
		})
		.catch(e=>{
		  console.log(e.data)
		})
	}

}
	},[])	

	return (
	<div style={sidebar}>
	<div style={logoContainer}>
	<img src={logo} style={logo_}/>
	<span>watstore</span>
	 </div>
		<div style={accountWrapper}>
		<div style={imageWraper}>
		<PersonSVGIcon style={image}/>
		</div>
		<p style={ text}>{sellerData.shop_name}</p>
		<p style={City}>{sellerData.city}</p>
		</div>
		<div style={linkWrapper}>

		<div id="links" style={selected=='home'?SelectedLink:link}>
		<p style={selected=='home'?link_black:link_p} onClick={()=>navigate('/dashboard')}>Home</p>
		<div id="underline"></div>
		</div>

			<div id="links" style={selected=='products'?SelectedLink:link}>
			<p style={selected=='products'?link_black:link_p} onClick={()=>navigate('/products')}>Products</p>
			<div id="underline"></div>
			</div>

			
			<div id="links" style={selected=='orders'?SelectedLink:link}>
			<p style={selected=='orders'?link_black:link_p} onClick={()=>navigate('/orders')}>Orders</p>
			<div id="underline"></div>
			</div>

			
		</div>
			<Button variant="outline-light" style={{marginTop:'5rem'}} onClick={action.action}>{action.text}</Button>
	</div>
	)
}